@import url(https://fonts.googleapis.com/css2?family=Akaya+Kanadaka&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@400;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@300;900&display=swap);
:root {
  --border-color: #64ffda;
}

* {
  box-sizing: border-box;
}

html {
  font-size: 1rem;
}

/* Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) {
  html {
    font-size: 1.2rem;
  }
}

/* Medium devices (tablets, 768px and up) */
@media (min-width: 768px) {
  html {
    font-size: 1.4rem;
  }
}

/* Large devices (desktops, 992px and up) */
@media (min-width: 992px) {
  html {
    font-size: 1.6rem;
  }
}

body {
  margin: 0;
  font-family: "Raleway", sans-serif;
  font-family: "Play", sans-serif;
  background: #0a192f;
  color: #64ffda;
}

.slides-container {
  height: 100vh;
  overflow-y: scroll;
  -ms-scroll-snap-type: y mandatory;
      scroll-snap-type: y mandatory;
}

.slide {
  height: 100vh;
  scroll-snap-align: center;
  padding: 1rem;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.border .border-solid-2 {
  border: 2px solid #64ffda;
  border: 2px solid var(--border-color);
}

.border-solid-3 {
  border: 3px solid #64ffda;
  border: 3px solid var(--border-color);
}

.border-solid-4 {
  border: 4px solid #64ffda;
  border: 4px solid var(--border-color);
}

.border-solid-5 {
  border: 5px solid #64ffda;
  border: 5px solid var(--border-color);
}

.border-on-hover-1:hover {
  border: 1px solid #64ffda;
  border: 1px solid var(--border-color);
}

.f-akaya {
  font-family: "Akaya Kanadaka", cursive;
}

.f-raleway,
.f-raleway-light {
  font-family: "Raleway", sans-serif;
  font-weight: 400;
}

.f-raleway-bold {
  font-family: "Raleway", sans-serif;
  font-weight: 900;
}

.f-sourcecode,
.f-sourcecode-light {
  font-family: "Source Code Pro", monospace;
  font-weight: 300;
}

.f-sourcecode-bold {
  font-family: "Source Code Pro", monospace;
  font-weight: 900;
}

.about-text {
  letter-spacing: 0.1em;
  line-height: 1.8em;
}

.avatar-xl {
  vertical-align: middle;
  width: 100px;
  height: 100px;
  border-radius: 50%;
}

/* Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) {
  .avatar-xl {
    width: 150px;
    height: 150px;
  }
}

:root {
  --backgroundColor: rgba(48, 98, 216, 1);
  --borderColor: rgba(48, 98, 216);
  --textColor: rgb(100, 255, 218);
}

.tooltip-inner {
  display: inline-block;
  position: relative;
  text-align: left;
  font-size: 0.8em;
  color: rgb(100, 255, 218);
  color: var(--textColor);
  background-color: rgba(48, 98, 216, 1);
  background-color: var(--backgroundColor);
  border-width: 2px;
  border-style: solid;
  border-color: rgba(48, 98, 216);
  border-color: var(--borderColor);
  padding: 10px;
  margin: 10px 0 0 0;
  -webkit-filter: drop-shadow(rgba(0, 0, 0, 0.4) 0 2px 3px);
          filter: drop-shadow(rgba(0, 0, 0, 0.4) 0 2px 3px);
}

.arrow {
  display: none !important;
}

.tooltip-inner::before,
.tooltip-inner::after {
  content: "";
  display: block;
  position: absolute;
  bottom: 100%;
  width: 0;
  height: 0;
  border-style: solid;
}

.tooltip-inner::after {
  right: calc(50% - 7px);
  border-color: transparent transparent rgba(48, 98, 216, 1) transparent;
  border-color: transparent transparent var(--backgroundColor) transparent;
  border-width: 7px;
}

.tooltip-inner::before {
  right: calc(50% - 10px);
  border-color: transparent transparent rgba(48, 98, 216) transparent;
  border-color: transparent transparent var(--borderColor) transparent;
  border-width: 10px;
}

.link {
  cursor: pointer;
}


